@import "https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css";

* {
  box-sizing: border-box;
}

input {
  all: unset;
  box-sizing: border-box;
  appearance: none;
}

body {
  background-color: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.noweet-input::placeholder {
  color: white;
  opacity: 0.9;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.form-input {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.form-btn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  cursor: pointer;
}

.cancel-btn {
  cursor: pointer;
  background-color: tomato;
}

/* Profile Page */

.profile-form {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.log-out {
  margin-top: 50px;
}

/* Nwitter Factory */

.factory-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.factory-input__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.factory-input__input {
  flex-grow: 1;
  height: 40px;
  padding: 0px 20px;
  color: white;
  border: 1px solid #04aaff;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}

.factory-input__arrow {
  position: absolute;
  right: 0;
  background-color: #04aaff;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
}

.factory-input__label {
  color: #04aaff;
  cursor: pointer;
}

.factory-input__label span {
  margin-right: 10px;
  font-size: 12px;
}

.factory-form__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.factory-form__attachment img {
  height: 80px;
  width: 80px;
  border-radius: 40px;
}

.factory-form__clear {
  color: #04aaff;
  cursor: pointer;
  text-align: center;
}

.factory-form__clear span {
  margin-right: 10px;
  font-size: 12px;
}

/* Noweet */

.noweet-edit .form-btn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.noweet {
  margin-bottom: 20px;
  background-color: white;
  width: 100%;
  max-width: 320px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
}

.noweet h4 {
  font-size: 14px;
}

.noweet img {
  right: -10px;
  top: 20px;
  position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-top: 10px;
}

.noweet__actions {
  position: absolute;
  right: 10px;
  top: 10px;
}

.noweet__actions span {
  cursor: pointer;
}

.noweet__actions span:first-child {
  margin-right: 10px;
}

/* Auth */
.auth-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.auth-btns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.auth-btn {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background: white;
  cursor: pointer;
}

/* AuthForm */
.auth-input {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
}

.auth-submit {
  text-align: center;
  background: #04aaff;
  color: white;
  margin-top: 10;
  cursor: pointer;
}

.auth-error {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.auth-switch {
  color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}
